                                        // In order to make the events work they have to have the classes "prev" and "next"
                                        // (plus namespace)
        sync: "",                       // Selector: Mirror the actions performed on this slider with another slider. Use with care.
        asNavFor: "",                   // Selector: Internal property exposed for turning the slider into a thumbnail navigation
                                        // for another slider.

